/*jshint esversion: 9 */

/**
 * External dependencies
 */
 import classnames from 'classnames';
 import filter from 'lodash/filter';
 
 /**
  * Internal dependencies
  */

 import Edit from './components/edit';
 import icons from './components/utils/icons';
 //import GalleryImageOutput from './components/gellery-image-output';
 import  MindImage from './components/utils/mind-image';
 import { BackgroundStyles } from './components/background';
 import { GlobalAttributes, GlobalTransforms, GlobalClasses, GlobalStyles } from './components/global';

 
 /**
  * WordPress dependencies
  */
 const { __ } = wp.i18n;
 const { createBlock } = wp.blocks;
 const { RichText, getFontSizeClass } = wp.editor;
 
 /**
  * Block constants
  */
 const name = 'Mindgallery';
 
 const title = __( 'Mindgallery' );

 const category = 'layout';
 
 //const icon = icons.stacked;
 const icon = 'format-image';
 
 const keywords = [
     __( 'gallery' ),
     __( 'images' ),
     __( 'photos' ),
 ];
 
 const blockAttributes = {
     ...GlobalAttributes,
 
     // Block specific attributes and overrides.
     align: {
         type: 'string',
         default: 'full',
     },
     captionStyle: {
         type: 'string',
     },
     fullwidth: {
         type: 'boolean',
         default: true,
     },
     gutter: {
         type: 'number',
         default: 0,
     },
     gutterMobile: {
         type: 'number',
         default: 0,
     },
 };
 
 const settings = {

    title: title,
    icon: icon,
    category: category, 
     
 
     description: __( 'Mindgallery settings for the default gallery.' ),
 
     keywords: keywords,
 
     attributes: blockAttributes,
 
     supports: {
         align: [ 'wide', 'full' ],
     },
 
     transforms: {
         from: [
             {
                 type: 'block',
                 blocks: [ 'blockgallery/masonry' ],
                 transform: ( attributes ) => (
                     createBlock( `blockgallery/${ name }`, {
                         ...GlobalTransforms( attributes ),
                     } )
                 ),
             },
             {
                 type: 'block',
                 blocks: [ 'blockgallery/carousel' ],
                 transform: ( attributes ) => (
                     createBlock( `blockgallery/${ name }`, {
                         ...GlobalTransforms( attributes ),
                     } )
                 ),
             },
             {
                 type: 'block',
                 blocks: [ 'blockgallery/thumbnails' ],
                 transform: ( attributes ) => (
                     createBlock( `blockgallery/${ name }`, {
                         ...GlobalTransforms( attributes ),
                     } )
                 ),
             },
             {
                 type: 'block',
                 blocks: [ 'blockgallery/offset' ],
                 transform: ( attributes ) => (
                     createBlock( `blockgallery/${ name }`, {
                         ...GlobalTransforms( attributes ),
                     } )
                 ),
             },
             {
                 type: 'block',
                 blocks: [ 'blockgallery/auto-height' ],
                 transform: ( attributes ) => (
                     createBlock( `blockgallery/${ name }`, {
                         ...GlobalTransforms( attributes ),
                     } )
                 ),
             },
             {
                 type: 'block',
                 blocks: [ 'core/gallery' ],
                 transform: ( attributes ) => (
                     createBlock( `blockgallery/${ name }`, {
                         ...GlobalTransforms( attributes ),
                     } )
                 ),
             },
             {
                 type: 'block',
                 isMultiBlock: true,
                 blocks: [ 'core/image' ],
                 transform: ( attributes ) => {
                     const validImages = filter( attributes, ( { id, url } ) => id && url );
                     if ( validImages.length > 0 ) {
                         return createBlock( `blockgallery/${ name }`, {
                             images: validImages.map( ( { id, url, alt, caption } ) => ( { id, url, alt, caption } ) ),
                             ids: validImages.map( ( { id } ) => id ),
                         } );
                     }
                     return createBlock( `blockgallery/${ name }` );
                 },
             },
             {
                 type: 'prefix',
                 prefix: ':stacked',
                 transform: function( content ) {
                     return createBlock( `blockgallery/${ name }`, {
                         content,
                     } );
                 },
             },
         ],
         to: [
             {
                 type: 'block',
                 blocks: [ 'core/gallery' ],
                 transform: ( attributes ) => (
                     createBlock( 'core/gallery', {
                         ...GlobalTransforms( attributes ),
                     } )
                 ),
             },
         ],
     },
 
     edit: Edit,
 
     save( { attributes, className } ) {
        console.log('SAVING IN THE INIT');
        console.log(attributes)
 
         const {
             captionColor,
             captions,
             customCaptionColor,
             customFontSize,
             fontSize,
             fullwidth,
             gutter,
             gutterMobile,
             images,
             linkTo,
             shadow,
             i_w,
             i_h,
         } = attributes;

         const new_attributes = function(){/*
            console.log('HOJ UZ TO BUDE');
            images.map( ( image ) => {
                const full_image = wp.data.select('core').getMedia(image.id);
       
                console.log('stufffffsssss');
                console.log(full_image);
       
                image.title = full_image ? full_image.title.raw : '';
                image.description = full_image ? full_image.description.raw : '';
                image.sizes = full_image ? full_image.media_details.sizes : '';
                image.i_w = full_image ? full_image.media_details.width : '';
                image.i_h = full_image ? full_image.media_details.height : '';
                
                let src_set = '';
                if(image.sizes != ''){
                    image.sizes.map( (size) => {
                        srcset += size.source_url + ' '+size.width+'w, ';
                    });
                }
                image.src_set = src_set;
               });
            */}();


         const wrapperClasses = classnames(
             ...GlobalClasses( attributes ), {
                 'has-fullwidth-images': fullwidth,
                 [ `has-margin` ] : gutter > 0,
             }
         );
 
         const wrapperStyles = {
             ...GlobalStyles( attributes ),
             ...BackgroundStyles( attributes ),
         };
 
         const fontSizeClass = getFontSizeClass( fontSize );
 
         const figureClasses = classnames(
             'blockgallery--figure', {
                 [ `has-margin-bottom-${ gutter }` ] : gutter > 0,
                 [ `has-margin-bottom-mobile-${ gutterMobile }` ] : gutterMobile > 0,
                 [ fontSizeClass ]: fontSizeClass,
         } );
 
         const captionClasses = classnames(
             'blockgallery--caption', {
                 [ fontSizeClass ]: fontSizeClass,
         } );
 
         const captionStyles = {
             fontSize: fontSizeClass ? undefined : customFontSize,
         };

         return (
             <div className={ className }>
                 <ul className={ wrapperClasses } style={ wrapperStyles }>
                     { images.map( ( image ) => {


                         let href;
                         switch ( linkTo ) {
                             case 'media':
                                 href = image.url;
                                 break;
                             case 'attachment':
                                 href = image.link;
                                 break;
                         }

 
                         const imgClasses = classnames(
                             image.id ? [ `wp-image-${ image.id }` ] : null
                         );

                         const img = <MindImage 
                            image = {image}
                         />;
 
                         return (
                             <li key={ image.id || image.url } className="blockgallery--item">
                                 <figure className={ figureClasses }>
                                     { href ? <a href={ href }>{ img }</a> : img }
                                     { captions && image.caption && image.caption.length > 0 && (
                                         <RichText.Content tagName="figcaption" className={ captionClasses } value={ image.caption } styles={ captionStyles }/>
                                     ) }
                                 </figure>
                             </li>
                         );
                     } ) }
                 </ul>
             </div>
         );
     },
 };
 export { name, title, icon, settings };
 